.cart-parent{
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 280px;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 5px;
    border: 0.5px solid #b2a5a5;
    background-color: #88838369;
    margin-top: 25px;
}

.image-cart{
    width: 100%;
    height: 75%;
}

.des-cart{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.add-cart{
    border-radius: 7px;
    border: 0.4px solid gray;
    width: 45%;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 200ms;
}

.add-cart:hover{
    background-color: transparent;
    color: white;
    font-weight: bold;
}