.parent-app{
    width: 80%;
    display: flex;
    flex-direction: column;
}

.cart-container{
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.manage-cart{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 90px;
}

.title-manage{
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    font-size: 27px;
    font-family: sans-serif
}

.clearAll{
    width: 15%;
    height: 30px;
    margin-top: 50px;
    border-radius: 5px;
    border: 0.5px solid gray;
    cursor: pointer;
    transition: 300ms;
}

.clearAll:hover{
    background-color: transparent;
    color: black;
    font-weight: bold;
}

.main-manage{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.main-manage div{
    width: 30%;
    height: 25px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid gray;
}

.titlesManageCart{
    width: 100%;
}

.allPrice{
    width: 40%;
    height: 100px;
    margin: auto;
    background-color: #b3acac;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: bold;
    margin-top: 20px;
    border-radius: 7px;
}