.addedCart{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.container-addedCart{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    height: 65px;
}

.container-addedCart div{
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-bascket{
    height: 50px;
    margin-right: 8px;
}

.container-addedCart span{
    display: flex;
    align-items: center;
}

.doing-cart button{
    border-radius: 7px;
    border: 0.4px solid gray;
    width: 25%;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 200ms;
}

.doing-cart button:hover{
    background-color: transparent;
    color: black;
    font-weight: bold;
}

.doing-cart input{
    width: 50px;
    margin-left: 5px;
}